var Lang = {};

/**
 *   ,   ,
 *      .
 *
 * @type {String}
 */
Lang.default = 'en';

/**
 *    ,     ,
 *    <html lang>,
 *  navigator.language
 *  navigator.userLanguage (EI)
 *    "ru"
 *
 * @param {String} language ISO 639-1  
 * @param {String} dictionary     
 * @return {String}  ISO 639-1   
 */
Lang.set = function(language, dictionary) {
    this.language = (
        language                      ||
        document.documentElement.lang ||
        navigator.language            ||
        navigator.userLanguage        ||
        this.default
    ).split('-')[0];

    if (dictionary) {
        this.dictionary = dictionary;
    }

    return this.language;
};

/**
 *       .
 *     ,      .
 *    ,     .
 *
 * @param  {Object} value  
 *                         
 * @return {String}         
 */
Lang.translate = function(value) {
    if (typeof value === 'object') {
        return value[this.language] || value[this.default];
    } else {
        return value;
    }
};

/**
 *      
 *
 * @param  {String} key    
 * @return {String}       
 */
Lang.get = function(key) {
    if (this.dictionary[key]) {
        return this.translate(this.dictionary[key]);
    } else {
        return '[' + key + ']';
    }
};
